

@font-face {
    font-family: 'Ambient';
    src: url('./../../fonts/Ambient/Ambient-Regular.eot');
    src: url('./../../fonts/Ambient/Ambient-Regular.eot?#iefix') format('embedded-opentype'),
        url('./../../fonts/Ambient/Ambient-Regular.woff2') format('woff2'),
        url('./../../fonts/Ambient/Ambient-Regular.woff') format('woff'),
        url('./../../fonts/Ambient/Ambient-Regular.ttf') format('truetype'),
        url('./../../fonts/Ambient/Ambient-Regular.svg#Ambient-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

